<template>
  <div id="app">
     <div class="wrap">
      <div class="result" v-show="visResult">
        <img src="/img2/11.jpg" class="resultImg" alt="result">
        <h3>Правильных ответов: {{trueAns}} из 10</h3>
        <div class="listAnswers">
        <ul>
          <li class="yellow">Ваши ответы:</li>
          <li v-for="yAns of allAnswers" :key="yAns">{{yAns}}</li>
        </ul>
        <ul>
          <li class="green">Правильные:</li>
          <li v-for="rAns of correctAns" :key="rAns">{{rAns}}</li>
        </ul>
        </div>
      </div>
      <div class="quiz" v-show="!visResult">
        <p class="counter">{{counter}}/10</p>
        <img :src="imgurl[this.flag]" alt="film">
        <h3>{{questions[this.flag]}}</h3>
        <div class="variants" @click="chooseBtn($event)">
           <button class="btn" :key="i" v-for="i of variants[this.varFlag]">
             {{i}}
           </button>
        </div>
        <div class="arrowWrap">
          <button :style="{'background':bg}" :disabled='dis' @click="nextLevel" class="arrow">
            Дальше
          </button>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
export default {
   data() {
     return {
       flag: 0,
       varFlag:1,
       dis: true,
       counter:1,
       visResult: false,
       trueAns:0,
       bg: '#c8d6e5',
       allAnswers: [],
       correctAns: ['Красную','Роберт Полсон','Голова','Aпельсин','Помни'
       ,'Сет и Ричи','Желтым','Навоз','Роршаха','Борис Бритва'],
       questions: [
         'Какую таблетку выбрал Нео?',
         'Его имя...',
         'Что в коробке?',
         'Заводной...?',
         'Фильм Нолана?',
         'Это?',
         'Какого цвета был ублюдок?',
         'Что ненавидит парень в красном?',
         'Не его заперли с ними, а их с ним...',
         'Неубиваемый...'
       ],
       variants: {
         '1': ['Cинюю','Виагру','Красную','От похмелья'],
         '2': ['Джек Воробей','Роберт Патисон','Роберт Полсон','Фродо Бэггинс'],
         '3': ['Голова','Другая коробка','Бомба','Торт'],
         '4': ['Банан','Человек','Aпельсин','Механизм'],
         '5': ['Не помню','Помни','Запомни','Вспомнить'],
         '6': ['Бивис и Баттхед','Том и Джерри','Винсент и Джулс','Сет и Ричи'],
         '7': ['Зеленым','Желтым','Черным','Белым'],
         '8': ['Сопляков','Навоз','Работать','Манную кашу'],
         '9': ['Роршаха','Шрека','Комедианта','Бэтмена'],
         '10':['Иван Молоток','Борис Бритва','Коля Топор','Витя Серп']
       },
       imgurl: [
         "/img2/1.jpg",
         "/img2/2.jpg",
         "/img2/3.jpg",
         "/img2/4.jpeg",
         "/img2/5.jpg",
         "img2/6.jpg",
         "/img2/7.jpg",
         "/img2/8.jpg",
         "/img2/9.jpg",
         "/img2/10.jpg"
       ]

       
     }
   },
   methods: {
     chooseBtn(event) {
        let btns = document.querySelectorAll('.btn');
        for (let i of btns) {
          i.style.backgroundColor = '#16a085'
          i.removeAttribute('data-check');
        }
        let val = event.target;
        if (val.tagName == 'BUTTON') {
          val.style.backgroundColor = '#ee5253'
          val.setAttribute('data-check','check')
          this.dis = false;
          this.bg = '#8e44ad'
          
        } else {
          val.removeAttribute('data-check')
          this.dis = true;
          this.bg = '#c8d6e5'
        }
        
       
     },
     nextLevel() {
       let btns2 = document.querySelectorAll('.btn');
        for (let i of btns2) {
          if (i.hasAttribute('data-check')) {

            this.allAnswers.push(i.innerHTML.trim())
          }
       }
       
       this.flag++;
       this.varFlag++;
       this.dis = true;
       this.bg = '#c8d6e5'
       this.counter++;
      if (this.counter == 11) {
         this.visResult = true;
         for(let i = 0; i<this.allAnswers.length; i++) {
           if (this.allAnswers[i] == this.correctAns[i]) {
              this.trueAns++
           }
         }
        
        }
      }
    }
}
  

</script>
